*{margin:0;padding:0;box-sizing:border-box}body{background-color:#296ca8;font-family:"Baloo Tamma 2",cursive}h1,h2,h3{color:#296ca8;text-align:center}p{color:#212121}

.header{height:100vh;background-position:center;background-size:cover;background-repeat:no-repeat;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}.header .header__h1{padding:0 10%;text-align:center;font-size:32px;line-height:48px;color:#fff;text-shadow:0 0 4px black}.header .header__h2{padding:0 10%;text-align:center;color:#fff;text-shadow:0 0 4px black}.header *{z-index:1}.header::after{content:'';position:absolute;top:0;left:0;height:100%;width:100%;background-color:rgba(0,0,0,0.3)}

*{margin:0;padding:0;box-sizing:border-box}body{background-color:#296ca8;font-family:"Baloo Tamma 2",cursive}h1,h2,h3{color:#296ca8;text-align:center}p{color:#212121}.mainBtn,.linkBtn,.submitBtn{background-color:#296ca8;color:#fff;text-decoration:none;outline:none;border:none;border-radius:5px;min-width:25%;min-height:25px;padding:2px 15px 2px 15px;cursor:pointer;margin:10px auto;box-shadow:2px 2px 5px rgba(0,0,0,0.5)}.iconBtn{cursor:pointer;font-size:24px;background-color:#296ca8;color:#fff;border:none;width:40px;height:40px;border-radius:50%;outline:none;box-shadow:2px 2px 5px rgba(0,0,0,0.5)}

.startDialog--active-false,.startHelpDialog--active-false{-webkit-transform:scale(0);transform:scale(0)}.startDialog--active-true,.startHelpDialog--active-true{-webkit-transform:scale(1);transform:scale(1)}

.startMenu{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}

*{margin:0;padding:0;box-sizing:border-box}body{background-color:#296ca8;font-family:"Baloo Tamma 2",cursive}h1,h2,h3{color:#296ca8;text-align:center}p{color:#212121}.form_container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.form_container .input_wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.form_container .input_wrapper label,.form_container .input_wrapper .text_input{color:#212121;padding:5px}.form_container .input_wrapper .text_input{border:1px solid #296ca8;border-radius:10px;outline:none}

*{margin:0;padding:0;box-sizing:border-box}body{background-color:#296ca8;font-family:"Baloo Tamma 2",cursive}h1,h2,h3{color:#296ca8;text-align:center}p{color:#212121}.rooms{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;background-color:#fff;width:100%}.rooms .room{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;position:relative;background-color:#296ca8;color:#fff;width:80%;border-radius:25px;font-size:14px;height:32px;line-height:32px;margin:3px}.rooms .room span{margin-left:10px}.rooms .room .room_btns{margin-right:10px}.rooms .room .edit-room-btn,.rooms .room .delete-room-btn{background-color:#296ca8;color:#fff;border:none;font-size:16px;padding:0 5px}

